.quick-knowing{
  padding: .625em 0;
  &__inner{
    display: grid;
    grid-template-columns: 11.875em calc(100% - 13.75em);
    gap: 1.875em;
    padding: 1em;
    border-radius: .5em;
    background-color: $lightGrayBg;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 63.75em;
    letter-spacing: 0.025em;
    @include grid100gap(.5em);
  }
}

.main{
  padding: .75em 0 2.5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 32.125em) 30.5em;
    grid-template-rows: 18.75em 11em;
    gap: 1.75em 1.625em;
  }
}

.banner-slider{
  position: relative;
  @media screen and (min-width: 993px){
    &._big{
      grid-row: 1/3;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      opacity: 0;
      background-color: #fff;
      &-active{
        opacity: 1;
      }
    }
    &-pagination{
      left: 4.125em;
      bottom: 3.75em;
      display: flex;
      align-items: center;
      justify-content: unset;
      width: unset;
      &-bullet{
        opacity: 1;
        background-color: #fff;
        width: .4375em;
        height: .4375em;
        border-radius: 50%;
        margin: 0 .5em !important;
        &-active{
          transform: scale(1.57);
        }
      }
    }
  }
}

.banner{
  height: 100%;
  border-radius: .5em;
  overflow: hidden;
  &__img{
    display: block;
    background-color: #ffffff;
    border-radius: .5em;
    position: relative;
    z-index: 2;
    &:hover{
      opacity: .8;
    }
    img{
      border-radius: inherit;
      width: 100%;
      height: auto;
      min-height: 100%;
      object-fit: contain;
    }
  }
}

.master{
  padding: 1em 1em 1em 1.25em;
  background-color: $lightGrayBg;
  border-radius: .5em;
  display: grid;
  grid-template-columns: auto 9.125em;
  gap: .75em 1.875em;
  &__content{

  }
  &__top{
    display: grid;
    grid-template-columns: 4.75em calc(100% - 5.75em);
    gap: 1em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .5em;
    }
  }
  &__info{
    @include grid100gap(.5em);
  }
  &__status{
    display: flex;
    align-items: center;
    &-pin{
      width: .6875em;
      height: .6875em;
      border-radius: 50%;
      flex-shrink: 0;
      display: block;
      margin-right: .375em;
    }
  }
  &__text{
    margin-top: .75em;
  }
  &__btn{
    height: 9.125em;
    flex-direction: column;
    text-align: center;
    border-radius: .5em;
    img{
      width: 3.875em;
      margin-bottom: .625em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.about{
  background-color: $grayBg;
  overflow: hidden;
}

.counts{
  padding: 2em 0;
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
    position: relative;
    z-index: 2;
  }
  &__rects1{
    width: 12em;
    position: absolute;
    bottom: -4.5625em;
    left: -1.375em;
  }
  &__rects2{
    width: 12.25em;
    position: absolute;
    right: -6.25em;
    bottom: 3em;
  }
}

.block-info{
  z-index: 2;
  position: relative;
  text-align: center;
  letter-spacing: 0.025em;
  margin-bottom: 1.5em;
  @include grid100gap(.25em);
  a{
    text-decoration: underline;
    color: $primary;
  }
}

.count-card{
  border-radius: .5em;
  background-color: #ffffff;
  padding: 2.125em 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  &__num{
    font-weight: 800;
    color: $primary;
    margin-right: 1em;
    span{
      letter-spacing: 0.025em;
      font-size: 5.375em;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 147%;
    letter-spacing: 0.025em;
  }
}


@media screen and (max-width: 992px){
  .quick-knowing{
    margin-top: .5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__img{
      width: 10.875em;
    }
  }

  .main{
    &__inner{
      grid-template-columns: 100%;
      grid-template-rows: unset;
      gap: 1.5em;
    }
    ._mob-hide{
      display: none;
    }
  }

  .banner-slider{
    .swiper{
      &-pagination{
        margin-top: 1em;
        position: static;
        justify-content: center;
        &-bullet{
          background-color: $textBlack;
        }
      }
    }
  }

  .master{
    grid-template-columns: 100%;
    gap: .75em;
    padding: 1em;
    &__top{
      grid-template-columns: 3.875em calc(100% - 4.875em);
    }
    &__btn{
      height: 3.25em;
      flex-direction: row;
      img{
        width: 2.0625em;
        margin: 0 .625em 0 0;
      }
    }
    &__text{
      .text16{
        font-size: .9375em;
      }
    }
  }

  .counts{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__rects1,
    &__rects2{
      display: none;
    }
  }

  .count-card{
    padding: 1em;
    &__num{
      span{
        font-size: 4em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .counts{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .count-card{
    padding: 1em;
    flex-direction: column;
    text-align: center;
    &__num{
      margin: 0 0 .5em;
    }
    br{
      display: none;
    }
  }
}
